@import "fonts";
@import "rouge-github";

/* ===== Base ===== */

body {
  background-color: #fff;
  padding: 50px;
  font: 14px/1.5 Palatino, 'Palatino Linotype', 'Palatino LT STD',
        'Latin Modern Roman', 'FZShuSong-Z01', 'Source Han Serif CN', serif;
  color: #727272;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6 {
  color: #222;
  margin: 0 0 20px;
}

p, ul, ol, table, pre, dl {
  margin: 0 0 20px;
}

h1, h2, h3 { line-height: 1.1; }
h1 { font-size: 28px; }
h2 { color: #393939; }
h3, h4, h5, h6 { color: #494949; }

a {
  color: #267CB9;
  text-decoration: none;
}

a:hover, a:focus {
  color: #069;
  font-weight: bold;
}

a small {
  font-size: 11px;
  color: #777;
  margin-top: -0.3em;
  display: block;
}

a:hover small { color: #777; }

.wrapper {
  width: 860px;
  margin: 0 auto;
}

blockquote {
  border-left: 1px solid #e5e5e5;
  margin: 0;
  padding: 0 0 0 20px;
  font-style: italic;
}

code, pre {
  font-family: Monaco, Bitstream Vera Sans Mono, Lucida Console, Terminal, Consolas,
               Liberation Mono, DejaVu Sans Mono, Courier New, monospace;
  color: #333;
}

pre {
  padding: 8px 15px;
  background: #f8f8f8;
  border-radius: 5px;
  border: 1px solid #e5e5e5;
  overflow-x: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  text-align: left;
  padding: 5px 10px;
  border-bottom: 1px solid #e5e5e5;
}

dt {
  color: #444;
  font-weight: 700;
}

th {
  color: #444;
}

img { max-width: 100%; }

kbd {
  background-color: #fafbfc;
  border: 1px solid #c6cbd1;
  border-bottom-color: #959da5;
  border-radius: 3px;
  box-shadow: inset 0 -1px 0 #959da5;
  color: #444d56;
  display: inline-block;
  font-size: 11px;
  line-height: 10px;
  padding: 3px 5px;
  vertical-align: middle;
}

/* ===== Layout (theme) ===== */

header {
  width: 270px;
  float: left;
  position: fixed;
  -webkit-font-smoothing: subpixel-antialiased;
}

section {
  width: 500px;
  float: right;
  padding-bottom: 50px;
}

footer {
  width: 270px;
  float: left;
  position: fixed;
  bottom: 50px;
  -webkit-font-smoothing: subpixel-antialiased;
}

small { font-size: 11px; }

hr {
  border: 0;
  background: #e5e5e5;
  height: 1px;
  margin: 0 0 20px;
}

/* Downloads bar (kept from theme even if unused) */
ul.downloads {
  list-style: none;
  height: 40px;
  padding: 0;
  background: #f4f4f4;
  border-radius: 5px;
  border: 1px solid #e0e0e0;
  width: 270px;
}

.downloads li {
  width: 89px;
  float: left;
  border-right: 1px solid #e0e0e0;
  height: 40px;
}

.downloads li:first-child a { border-radius: 5px 0 0 5px; }
.downloads li:last-child a { border-radius: 0 5px 5px 0; }

.downloads a {
  line-height: 1;
  font-size: 11px;
  color: #676767;
  display: block;
  text-align: center;
  padding-top: 6px;
  height: 34px;
}

.downloads a:hover, .downloads a:focus {
  color: #675C5C;
  font-weight: bold;
}

.downloads ul a:active { background-color: #f0f0f0; }

strong {
  color: #222;
  font-weight: 700;
}

.downloads li + li + li {
  border-right: none;
  width: 89px;
}

.downloads a strong {
  font-size: 14px;
  display: block;
  color: #222;
}

/* ===== Responsive (theme) ===== */

@media print, screen and (max-width: 960px) {
  div.wrapper {
    width: auto;
    margin: 0;
  }

  header, section, footer {
    float: none;
    position: static;
    width: auto;
  }

  header {
    padding-right: 320px;
  }

  section {
    border: 1px solid #e5e5e5;
    border-width: 1px 0;
    padding: 20px 0;
    margin: 0 0 20px;
  }

  header a small { display: inline; }

  header ul {
    position: absolute;
    right: 50px;
    top: 52px;
  }
}

@media print, screen and (max-width: 720px) {
  body { word-wrap: break-word; }
  header { padding: 0; }
  header ul, header p.view { position: static; }
  pre, code { word-wrap: normal; }
}

@media print, screen and (max-width: 480px) {
  body { padding: 15px; }
  .downloads { width: 99%; }
  .downloads li, .downloads li + li + li { width: 33%; }
}

@media print {
  body {
    padding: 0.4in;
    font-size: 12pt;
    color: #444;
  }
}

/* =========================================================
   Custom: mobile title + hide main H1 on mobile
   ========================================================= */

/* Hidden by default (desktop/tablet) */
.mobile-title {
  display: none;
}

/* Show site title at top on mobile */
@media screen and (max-width: 720px) {
  .mobile-title {
    display: block;
    text-align: center;
    font-size: 28px;
    font-weight: 700;
    color: #222;
    margin: 10px 0 16px;
  }
}

/* Hide the main content H1 on mobile */
@media screen and (max-width: 720px) {
  section h1,
  section > h1:first-child {
    display: none !important;
  }
}

/* =========================================================
   Custom: sidebar styling (centered, bold headings, spaced)
   ========================================================= */

header .sidebar {
  text-align: center;
}

header .avatar {
  display: block;
  margin: 0 auto 24px;
}

header .sidebar-cv {
  display: inline-block;
  margin: 12px 0 32px;
  font-size: 26px;
  font-weight: 500;
  color: #8b0000;
}

header .sidebar-section {
  margin-bottom: 34px;
}

header .sidebar-title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 14px;
  color: #222;
}

header .sidebar-text {
  font-size: 18px;
  line-height: 1.9;
  color: #333;
}

header .sidebar-email {
  display: inline-block;
  font-size: 18px;
  margin-top: 6px;
  color: #8b0000;
  text-decoration: none;
}

header .sidebar-icons {
  margin-top: 24px;
  display: flex;
  justify-content: center;
  gap: 22px;
}

header .sidebar-icons a {
  color: #111;
  text-decoration: none;
}

header .sidebar-icons a:hover {
  color: #8b0000;
}

/* Optional: slightly larger icons */
header .sidebar-icons svg {
  display: block;
}


/* =========================================================
   MOBILE TITLE + HEADER ORDER (FINAL FIX)
   ========================================================= */

/* 1. NEVER show mobile title on desktop */
.mobile-title {
  display: none !important;
}

/* 2. Mobile layout fix */
@media screen and (max-width: 720px) {

  /* Make header a vertical flex container */
  header {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  /* Show title on mobile */
  .mobile-title {
    display: block !important;
    order: 1;                 /* ABOVE avatar */
    font-size: 32px;          /* bigger */
    font-weight: 700;
    text-align: center;
    color: #222;
    margin: 0 0 14px;
  }

  /* Avatar comes after title */
  header .avatar {
    order: 2;
    margin: 0 0 24px;
  }

  /* Sidebar content below avatar */
  header .sidebar {
    order: 3;
    text-align: center;
  }
}

/* 3. Hide main content H1 on mobile */
@media screen and (max-width: 720px) {
  section h1,
  section > h1:first-child {
    display: none !important;
  }
}
